import { ChangeDetectionStrategy, Component, Input, OnInit, } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, ValidatorFn, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
//
import { MatDialog, MatDialogRef } from '@angular/material';
import { TranslateService } from '@ngx-translate/core';
//
import { Face, State } from '../../../../../ui/modules/images-card/models/face';
import { BaseReactiveFormComponent } from '../../../../../ui/components/base-reactive-form/base-reactive-form-component';
import { Shop, WeekHours } from '../../models/shops';
import { Brand } from '../../../ms-brands/models/brand';
import { Currency, CURRENCY } from '../../models/currency';
import { Category } from '../../../ms-categories/models/category';
import { Collection } from '../../../ms-collections/models/collection';
import { Country, COUNTRIES } from '../../models/country';
import { Rank, RANK } from '../../models/rank';
import { Region, REGIONS } from '../../models/regions';
import { ShopsImgesService } from '../../services/shops-images.service';
import { ShopsSellingStyleModalComponent } from '../shops-selling-style-modal/shops-selling-style-modal.component';

import { ErrorHandlingService } from '../../../../../error-handling/services/error-handling.service';
import { HandledError } from '../../../../../error-handling/models/handled-error';
import { Url } from '../../../ms-urls/models/urls';

const errorKey = 'Error';

@Component({
    selector: 'edit-shop-form',
    templateUrl: './edit-shop-form.component.html',
    styleUrls: ['./edit-shop-form.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class EditShopFormComponent extends BaseReactiveFormComponent<Shop> implements OnInit {

    currency: Currency[] = CURRENCY;

    faces: FormControl;

    modalRef: MatDialogRef<ShopsSellingStyleModalComponent>;

    principal: Face;

    shop: Shop;

    countries: Country[] = COUNTRIES;

    regions: Region[] = REGIONS;

    rank: Rank[] = RANK;

    @Input() brands: Array<Brand>;

    @Input() categories: Array<Category>;

    @Input() collections: Array<Collection>;

    @Input() faceList: Array<Face> = [];

    @Input() shopId: string;

    urls: Array<Url>;

    constructor(
        public activatedRoute: ActivatedRoute,
        public dialog: MatDialog,
        public errorHandlingService: ErrorHandlingService,
        private formBuilder: FormBuilder,
        public shopsImgesService: ShopsImgesService,
        translateService: TranslateService) {
        super(translateService);
        //setTranslations(this.translateService, TRANSLATIONS);TODO
    }

    ngOnInit() {
        this.data.workingHours.forEach((workinHour) => {
            //this.isLinked(shop.id);
            if(!workinHour.openHour && !workinHour.closeHour){
                workinHour.notWorking = true;
            }else{
                workinHour.notWorking = false;
            }
        });

        let validationsErrors: any[] = [
            {
                type: 'required',
                key: 'Required Field',
                params: null,
                translation: ''
            }
        ];

        this.validationErrorMessages = validationsErrors;

        this.faceList.forEach((image) => {
            if (image.imgUrl === this.data.mainImage) {
                this.principal = image;
            }
        });

        this.createFormGroup();
    }

    createFormGroup() {
        this.faces = this.formBuilder.control(this.faceList);
        this.formGroup = new FormGroup({
            faces: this.faces,
            name: new FormControl(this.data.name, [Validators.required]),
            address: new FormControl(this.data.address),
            shippingDetails: new FormControl(this.data.shippingDetails),
            description: new FormControl(this.data.description),
            makeDeal: new FormControl(this.data.makeDeal),
            currency: new FormControl(this.data.currency),
            rank: new FormControl(this.data.rank),
            region: new FormControl(this.data.region),
            country: new FormControl(this.data.country),
            brand: new FormControl(this.data.brand),
            active: new FormControl(true),
            category: new FormControl(this.data.category),
            trackingListBaseUrl: new FormControl(this.data.trackingListBaseUrl),
            workingHours: this.formBuilder.array([
                this.formBuilder.group({
                    dayOfWeekMonday: 1,
                    openHourMonday: new FormControl(/*this.findWorkinHorsInData(1).openHour*/),
                    closeHourMonday: new FormControl(/*this.findWorkinHorsInData(1).closeHour*/),
                    notWorkingMonday: new FormControl(/*this.findWorkinHorsInData(1).closeHour*/),
                }),
                this.formBuilder.group({
                    dayOfWeekTuesday: 2,
                    openHourTuesday: new FormControl(this.data.category),
                    closeHourTuesday: new FormControl(this.data.category),
                }),
                this.formBuilder.group({
                    dayOfWeekWednesday: 3,
                    openHourWednesday: new FormControl(this.data.category),
                    closeHourWednesday: new FormControl(this.data.category),
                }),
                this.formBuilder.group({
                    dayOfWeekThursday: 4,
                    openHourThursday: new FormControl(this.data.category),
                    closeHourThursday: new FormControl(this.data.category),
                }),
                this.formBuilder.group({
                    dayOfWeekFriday: 5,
                    openHourFriday: new FormControl(this.data.category),
                    closeHourFriday: new FormControl(this.data.category),
                }),
                this.formBuilder.group({
                    dayOfWeekSaturday: 6,
                    openHourSaturday: new FormControl(this.data.category),
                    closeHourSaturday: new FormControl(this.data.category),
                }),
                this.formBuilder.group({
                    dayOfWeekSunday: 7,
                    openHourSunday: new FormControl(this.data.category),
                    closeHourSunday: new FormControl(this.data.category),
                })

            ]),


            //createdBy: new FormControl(this.data.createdBy, [Validators.required]),TODO   
        });

    }

    submitClicked() {
        if (this.formGroup.valid) {
            this.data.workingHours = this.formGroup.get('workingHours').value;
            this.accept.emit(this.data);
        } else {
            this.triggerValidation();
        }
    }

    showModal() {
        this.modalRef = this.dialog.open(ShopsSellingStyleModalComponent, {
            height: '800px',
            width: '60%',
            data: { face: this.shop }
        });
    }


    onDeleteFace(face: Face) {
        if (face.id) {
            this.shopsImgesService.deleteShopImage(this.data.id, face.id).subscribe(response => {
            },
                (error: HandledError) => this.errorHandlingService.handleUiError(errorKey, error));

        }
    }

    buidlWorkingHours(workinHour: Array<any>) {

        workinHour[0]
        let realWorkinHours: Array<WeekHours> = [];
        let monday: WeekHours = {
            dayOfWeek: 0,
            openHour: '',
            closeHour: '',
        };
        //monday
        console.log(workinHour[0]);
        monday.dayOfWeek = workinHour[0].dayOfWeekMonday;
        monday.openHour = workinHour[0].openHourMonday;
        monday.closeHour = workinHour[0].closeHourMonday;
        realWorkinHours = [...realWorkinHours, monday];
        //tuesday
        let tuesday: WeekHours = {
            dayOfWeek: 0,
            openHour: '',
            closeHour: '',
        };
        tuesday.dayOfWeek = workinHour[1].dayOfWeekTuesday;
        tuesday.openHour = workinHour[1].openHourTuesday;
        tuesday.closeHour = workinHour[1].closeHourTuesday;
        realWorkinHours = [...realWorkinHours, tuesday];
        //wednesday
        let wednesday: WeekHours = {
            dayOfWeek: 0,
            openHour: '',
            closeHour: '',
        };
        wednesday.dayOfWeek = workinHour[2].dayOfWeekWednesday;
        wednesday.openHour = workinHour[2].openHourWednesday;
        wednesday.closeHour = workinHour[2].closeHourWednesday;
        realWorkinHours = [...realWorkinHours, wednesday];
        //thursday
        let thursday: WeekHours = {
            dayOfWeek: 0,
            openHour: '',
            closeHour: '',
        };
        thursday.dayOfWeek = workinHour[3].dayOfWeekThursday;
        thursday.openHour = workinHour[3].openHourThursday;
        thursday.closeHour = workinHour[3].closeHourThursday;
        realWorkinHours = [...realWorkinHours, thursday];
        //friday
        let friday: WeekHours = {
            dayOfWeek: 0,
            openHour: '',
            closeHour: '',
        };
        friday.dayOfWeek = workinHour[4].dayOfWeekFriday;
        friday.openHour = workinHour[4].openHourFriday;
        friday.closeHour = workinHour[4].closeHourFriday;
        realWorkinHours = [...realWorkinHours, friday];
        //saturday
        let saturday: WeekHours = {
            dayOfWeek: 0,
            openHour: '',
            closeHour: '',
        };
        saturday.dayOfWeek = workinHour[5].dayOfWeekSaturday;
        saturday.openHour = workinHour[5].openHourSaturday;
        saturday.closeHour = workinHour[5].closeHourSaturday;
        realWorkinHours = [...realWorkinHours, saturday];
        //sunday
        let sunday: WeekHours = {
            dayOfWeek: 0,
            openHour: '',
            closeHour: '',
        };
        sunday.dayOfWeek = workinHour[6].dayOfWeekSunday;
        sunday.openHour = workinHour[6].openHourSunday;
        sunday.closeHour = workinHour[6].closeHourSunday;
        realWorkinHours = [...realWorkinHours, sunday];

        console.log(realWorkinHours);
        return realWorkinHours;
    }

    findWorkinHorsInData(day: number) : WeekHours{
        return this.data.workingHours.find((val) => val.dayOfWeek === day);
    }
}

